//     Replicate API  n8n
const N8N_BASE_URL = 'https://n8n.89.117.63.81.sslip.io/webhook';


class ReplicateService {
  constructor() {
    this.baseURL = N8N_BASE_URL;
  }

  //    base64 data URL
  async fileToDataUrl(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => resolve(reader.result);
      reader.onerror = reject;
      reader.readAsDataURL(file);
    });
  }

  //      (  imgbb, cloudinary  )
  async uploadImage(file) {
    try {
      //     data URL
      const dataUrl = await this.fileToDataUrl(file);
      return dataUrl;
    } catch (error) {
      console.error('Error uploading image:', error);
      throw new Error('Failed to upload image');
    }
  }

  //    
  async createPrediction(humanImg, garmImg, garmentDescription = 'fashionable clothing') {
    try {
      console.log(' Using n8n webhook:', `${this.baseURL}/replicate`);
      
      const response = await fetch(`${this.baseURL}/replicate`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          human_img: humanImg,
          garm_img: garmImg,
          garment_des: garmentDescription
        })
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.message || `HTTP ${response.status}: ${response.statusText}`);
      }

      const prediction = await response.json();
      return prediction;
    } catch (error) {
      console.error('Error creating prediction:', error);
      
      //  n8n  ,   
      if (error.message.includes('Failed to fetch') || error.message.includes('ERR_CONNECTION_REFUSED')) {
        throw new Error('n8n webhook .   workflow   n8n   ' + this.baseURL);
      }
      
      throw error;
    }
  }

  //   
  async getPrediction(predictionId) {
    try {
      const response = await fetch(`${this.baseURL}/replicate-status?id=${predictionId}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        }
      });

      if (!response.ok) {
        throw new Error(`Failed to get prediction status: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error('Error getting prediction:', error);
      throw error;
    }
  }

  //     try-on
  async generateTryOn(personImage, clothingImage, garmentDescription = "clothing item") {
    try {
      console.log(' Starting try-on generation with n8n + Replicate...');
      
      //    base64 data URLs   File 
      const personImageData = await this.fileToDataURL(personImage);
      const clothingImageData = await this.fileToDataURL(clothingImage);

      //  prediction  n8n
      const response = await fetch(`${this.baseURL}/replicate`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          human_img: personImageData,
          garm_img: clothingImageData,
          garment_des: garmentDescription
        })
      });

      //  ,       ,
      //      JSON (    )
      const clonedResponse = response.clone();
      const rawResponseText = await clonedResponse.text();
      console.log(' Raw response from n8n webhook:', rawResponseText);
      console.log(' Response status:', response.status, response.statusText);

      if (!response.ok) {
        let errorData = { message: `HTTP ${response.status}: ${response.statusText}` };
        //        ,   
        if (rawResponseText) {
          try {
            //    JSON,  n8n  JSON-
            const parsedError = JSON.parse(rawResponseText);
            errorData.message = parsedError.message || parsedError.error || rawResponseText;
          } catch (e) {
            //   JSON,     
            errorData.message = rawResponseText;
          }
        }
        console.error(' Error response from n8n:', errorData.message);
        throw new Error(errorData.message);
      }

      // ,  response.ok,    response  JSON
      const prediction = await response.json();
      console.log(' Prediction created via n8n:', prediction.id);

      //     wait   
      return {
        id: prediction.id,
        status: prediction.status,
        wait: (onProgress) => this.waitForCompletion(prediction.id, onProgress)
      };

    } catch (error) {
      console.error(' n8n + Replicate generation failed:', error);
      
      //  n8n  ,   
      if (error.message.includes('Failed to fetch') || error.message.includes('ERR_CONNECTION_REFUSED')) {
        throw new Error('n8n .  :\n1. n8n   ' + this.baseURL + '\n2. Workflow \n3. Webhook  ');
      }
      
      throw error;
    }
  }

  //      
  async waitForCompletion(predictionId, onProgress) {
    const maxAttempts = 60; // 5  
    const pollInterval = 5000; // 5   
    
    let attempts = 0;
    
    const poll = async () => {
      try {
        attempts++;
        
        //   prediction  n8n
        const statusUrl = `${this.baseURL}/replicate-status?id=${predictionId}`;

        const response = await fetch(statusUrl, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          }
        });

        if (!response.ok) {
          throw new Error(`Failed to get prediction status: ${response.statusText}`);
        }

        const prediction = await response.json();
        
        //     
        let progress = 0;
        let status = 'processing';
        let message = 'Processing your try-on...';

        switch (prediction.status) {
          case 'starting':
            progress = 10;
            status = 'starting';
            message = 'Initializing AI model...';
            break;
          case 'processing':
            //     
            progress = Math.min(20 + (attempts * 10), 90);
            status = 'processing';
            message = 'AI is creating your try-on...';
            break;
          case 'succeeded':
            progress = 100;
            status = 'completed';
            message = 'Try-on completed successfully!';
            if (onProgress) onProgress(progress, status, message);
            return prediction;
          case 'failed':
            throw new Error(prediction.error || 'Generation failed');
          case 'canceled':
            throw new Error('Generation was canceled');
          default:
            //        ,
            //  ESLint  default case.
            //   ,   .
            console.warn(`Unknown prediction status: ${prediction.status}`);
            break;
        }

        //  callback  
        if (onProgress) {
          onProgress(progress, status, message);
        }

        //    ,  polling
        if (prediction.status === 'starting' || prediction.status === 'processing') {
          if (attempts >= maxAttempts) {
            throw new Error('Generation timeout - please try again');
          }
          
          await new Promise(resolve => setTimeout(resolve, pollInterval));
          return poll();
        }

        return prediction;

      } catch (error) {
        console.error(' Polling error:', error);
        throw error;
      }
    };

    return poll();
  }

  //     File  Data URL
  async fileToDataURL(imageInput) {
    console.log(' Converting to data URL:', imageInput);
    
    //     (URL),   
    if (typeof imageInput === 'string') {
      return imageInput;
    }

    //     file  ( UploadPage)
    if (imageInput && typeof imageInput === 'object' && imageInput.file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result);
        reader.onerror = reject;
        reader.readAsDataURL(imageInput.file);
      });
    }

    //    File 
    if (imageInput instanceof File) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result);
        reader.onerror = reject;
        reader.readAsDataURL(imageInput);
      });
    }

    //     url ,  url
    if (imageInput && typeof imageInput === 'object' && imageInput.url) {
      return imageInput.url;
    }

    console.error(' Invalid file format:', imageInput);
    throw new Error(`Invalid file format: received ${typeof imageInput}, expected File object or string URL`);
  }

  //     (   n8n)
  async getModelInfo() {
    console.log(' Model info not available through n8n webhook');
    return null;
  }

  //     
  async generateVariations(personImage, clothingImage) {
    const descriptions = [
      'elegant and sophisticated style',
      'casual and comfortable look', 
      'trendy and fashionable outfit',
      'professional business attire'
    ];

    const results = [];
    
    for (let i = 0; i < descriptions.length; i++) {
      try {
        const result = await this.generateTryOn(personImage, clothingImage, descriptions[i]);
        results.push({
          id: result.id,
          description: descriptions[i],
          title: `Style Variation ${i + 1}`,
          promise: result
        });
      } catch (error) {
        console.error(`Failed to generate variation ${i + 1}:`, error);
      }
    }

    return results;
  }
}

//  singleton instance
const replicateService = new ReplicateService();
export default replicateService; 