//    ,  HEVC  JPEG
import heicPolyfill from './heicPolyfill';
import imagePreprocessor from './imagePreprocessor';

class ImageConverterService {
  constructor() {
    this.canvas = document.createElement('canvas');
    this.ctx = this.canvas.getContext('2d');
  }

  // ,    HEVC/HEIF 
  isHEVCFormat(file) {
    return heicPolyfill.canProcess(file);
  }

  // ,     iPhone
  isiPhoneImage(file) {
    return imagePreprocessor.needsAdvancedProcessing(file);
  }

  //  HEVC  JPEG   
  async convertHEVCToJPEG(file, quality = 0.95) {
    try {
      //      HEIC
      const convertedFile = await heicPolyfill.convertHEIC(file);
      
      //       (  ),
      //     canvas
      if (convertedFile === file) {
        return await this.fallbackConversion(file, quality);
      }
      
      return convertedFile;
      
    } catch (error) {
      console.warn('HEIC polyfill failed, trying fallback:', error.message);
      return await this.fallbackConversion(file, quality);
    }
  }

  // Fallback     Image + Canvas
  async fallbackConversion(file, quality = 0.95) {
    return new Promise((resolve, reject) => {
      const img = new Image();
      
      //  URL  
      const url = URL.createObjectURL(file);
      
      img.onload = () => {
        try {
          //  canvas   
          this.canvas.width = img.naturalWidth;
          this.canvas.height = img.naturalHeight;
          
          //  canvas
          this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
          
          //    canvas   
          this.ctx.imageSmoothingEnabled = true;
          this.ctx.imageSmoothingQuality = 'high';
          this.ctx.drawImage(img, 0, 0);
          
          //   blob    JPEG
          this.canvas.toBlob((blob) => {
            if (blob) {
              //   File 
              const convertedFile = new File(
                [blob], 
                file.name.replace(/\.(heic|heif)$/i, '.jpg'),
                { type: 'image/jpeg' }
              );
              
              console.log(` HEVC : ${file.name} (${(file.size/1024/1024).toFixed(2)}MB)  ${convertedFile.name} (${(convertedFile.size/1024/1024).toFixed(2)}MB)`);
              resolve(convertedFile);
            } else {
              reject(new Error('   HEVC '));
            }
          }, 'image/jpeg', quality);
          
        } catch (error) {
          reject(error);
        } finally {
          //  
          URL.revokeObjectURL(url);
        }
      };
      
      img.onerror = () => {
        URL.revokeObjectURL(url);
        reject(new Error('   HEVC   '));
      };
      
      //  crossOrigin   CORS 
      img.crossOrigin = 'anonymous';
      img.src = url;
    });
  }

  //    (    )
  async optimizeImage(file, maxWidth = 2048, maxHeight = 2048, quality = 0.9) {
    return new Promise((resolve, reject) => {
      const img = new Image();
      const url = URL.createObjectURL(file);
      
      img.onload = () => {
        try {
          let { width, height } = img;
          
          //      
          if (width > maxWidth || height > maxHeight) {
            const ratio = Math.min(maxWidth / width, maxHeight / height);
            width = Math.round(width * ratio);
            height = Math.round(height * ratio);
          }
          
          this.canvas.width = width;
          this.canvas.height = height;
          
          this.ctx.clearRect(0, 0, width, height);
          this.ctx.imageSmoothingEnabled = true;
          this.ctx.imageSmoothingQuality = 'high';
          this.ctx.drawImage(img, 0, 0, width, height);
          
          this.canvas.toBlob((blob) => {
            if (blob) {
              const optimizedFile = new File(
                [blob], 
                file.name,
                { type: file.type }
              );
              
              console.log(` : ${(file.size/1024/1024).toFixed(2)}MB  ${(optimizedFile.size/1024/1024).toFixed(2)}MB`);
              resolve(optimizedFile);
            } else {
              reject(new Error('   '));
            }
          }, file.type, quality);
          
        } catch (error) {
          reject(error);
        } finally {
          URL.revokeObjectURL(url);
        }
      };
      
      img.onerror = () => {
        URL.revokeObjectURL(url);
        reject(new Error('     '));
      };
      
      img.crossOrigin = 'anonymous';
      img.src = url;
    });
  }

  //  :   
  async processImage(file) {
    console.log(`   : ${file.name}, : ${(file.size / 1024).toFixed(2)} KB`);
    try {
      let processedFile = file;
      
      //   HEVC,   JPEG
      if (this.isHEVCFormat(file)) {
        console.log('  HEVC ,   JPEG...');
        processedFile = await this.convertHEVCToJPEG(file, 0.95);
      }
      
      //     iPhone,   
      if (this.isiPhoneImage(file)) {
        console.log('   iPhone,   ...');
        processedFile = await imagePreprocessor.preprocessForAI(processedFile, {
          normalizeColors: false,  //   
          enhanceQuality: false,   //   
          optimizeSize: true,      //     AI
          stripMeta: true,         //    
          targetWidth: 768,
          targetHeight: 1024
        });
      } else {
        //    -  
        const maxSizeForOptimization = 8 * 1024 * 1024; // 8MB
        if (processedFile.size > maxSizeForOptimization) {
          console.log('  , ...');
          processedFile = await this.optimizeImage(processedFile, 2048, 2048, 0.9);
        }
      }
      
      return processedFile;
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }

  //    
  async getImageInfo(file) {
    return new Promise((resolve, reject) => {
      const img = new Image();
      const url = URL.createObjectURL(file);
      
      img.onload = () => {
        const info = {
          width: img.naturalWidth,
          height: img.naturalHeight,
          aspectRatio: img.naturalWidth / img.naturalHeight,
          isPortrait: img.naturalHeight > img.naturalWidth,
          isLandscape: img.naturalWidth > img.naturalHeight,
          isSquare: img.naturalWidth === img.naturalHeight,
          isiPhoneImage: this.isiPhoneImage(file),
          isHEVC: this.isHEVCFormat(file)
        };
        
        URL.revokeObjectURL(url);
        resolve(info);
      };
      
      img.onerror = () => {
        URL.revokeObjectURL(url);
        reject(new Error('     '));
      };
      
      img.src = url;
    });
  }

  //    Base64
  fileToBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  }
}

//  singleton instance
const imageConverter = new ImageConverterService();
export default imageConverter; 